.row{
    margin-left: 20px;
    color: #ffff;
    padding-top: 30px;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;

}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    max-height: 250px;
    margin-right: 15px;
    cursor: pointer;

}
.poster:hover{
    transform: scale(1.1);
}
.small_poster{
    max-height: 150px;
    margin-right: 15px;
    cursor: pointer;
}
.small_poster:hover{
    transform: scale(1.1);
}
.netflix_orginals{
    font-size:24px; font-weight:400; text-transform:uppercase;
  }
  .netflix_orginals:before {
    background-color: #c50000;
    border-radius: 0.25rem;
    content: '';
    display: block;
    height: 0.25rem;
    width: 42px;
    margin-bottom: 1.25rem;
}

.title{
    font-size: 2.7rem;
    font-weight: 800;
    padding-bottom: 0.3rem;


}