.banner{
    background-size: cover;
    height: 500px;
    color: white;
    padding-top: 20px;
    margin-top: 20px;

}
.content{
    padding-top: 120px;
    height: 190px;
    padding-left: 20px;
}
.title{
    font-size: 2.7rem;
    font-weight: 800;
    padding-bottom: 0.3rem;


}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}
.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1.2rem;
    height: 80px;
    max-width: 500px;
}
.fade_bottom{
    height: 450px;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}